import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import styled from 'styled-components';
import { animated, useSpring, config } from 'react-spring';
import Layout from '../components/layout';
import GridItem from '../components/grid-item';
import SEO from '../components/SEO';
import { ChildImageSharp } from '../types';

type PageProps = {
  data: {
    firstProject: {
      title: string;
      slug: string;
      cover: ChildImageSharp;
    };
    threeProjects: {
      nodes: {
        title: string;
        slug: string;
        cover: ChildImageSharp;
      }[];
    };
    aboutUs: ChildImageSharp;
    instagram: ChildImageSharp;
  };
};

const Area = styled(animated.div)`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 35vw 40vw 35vw;
  grid-template-areas:
    'instagram instagram instagram'
    'three-projects three-projects three-projects'
    'first-project about-us about-us';

  @media (max-width: ${(props) => props.theme.breakpoints[3]}) {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 35vw 30vw 30vw 30vw;

    grid-template-areas:
    'instagram instagram instagram instagram'
      'three-projects three-projects three-projects three-projects'
      'three-projects three-projects three-projects three-projects'
      'first-project first-project about-us about-us'
  }

  @media (max-width: ${(props) => props.theme.breakpoints[1]}) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, 38vw);

    grid-template-areas:
    'instagram instagram';
      'three-projects three-projects'
      'three-projects three-projects'
      'three-projects three-projects'
      
      'first-project about-us'
  }

  @media (max-width: ${(props) => props.theme.breakpoints[0]}) {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(8, 60vw);

    grid-template-areas:
      'instagram'
      'instagram'
      'instagram'
      'three-projects'
      'three-projects'
      'three-projects'
      'first-project'
      'about-us';
  }
`;

const FirstProject = styled(GridItem)`
  grid-area: first-project;
`;

const AboutUs = styled(GridItem)`
  grid-area: about-us;
`;

const ThreeProjects = styled.div`
  grid-area: three-projects;
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  @media (max-width: ${(props) => props.theme.breakpoints[1]}) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
`;
const Instagram = styled.div`
  grid-area: instagram;
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  @media (max-width: ${(props) => props.theme.breakpoints[1]}) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
`;
const Instagram1 = styled(GridItem)`
  grid-area: instagram;
`;

const Index: React.FunctionComponent<PageProps> = ({
  data: { firstProject, threeProjects, aboutUs, instagramFiles },
}) => {
  const pageAnimation = useSpring({
    config: config.slow,
    from: { opacity: 0 },
    to: { opacity: 1 },
  });

  return (
    <Layout>
      <SEO />
      <Area style={pageAnimation}>
        {firstProject.nodes.map((fp) => (
          <FirstProject to={fp.slug} aria-label={`View project "${fp.title}"`}>
            <Img alt={fp.title} fluid={fp.cover.childImageSharp.fluid} />
          </FirstProject>
        ))}
        <AboutUs to="/about" aria-label="Visit my about page">
          <Img fluid={aboutUs.childImageSharp.fluid} />
          <span>About</span>
        </AboutUs>
        <ThreeProjects>
          {threeProjects.nodes.map((project) => (
            <GridItem
              to={project.slug}
              key={project.slug}
              aria-label={`View project "${project.title}"`}
            >
              <Img
                alt={project.title}
                fluid={project.cover.childImageSharp.fluid}
              />
            </GridItem>
          ))}
        </ThreeProjects>
        <Instagram>
          {instagramFiles.nodes.map((project) => (
            <GridItem
              to={project.slug}
              key={project.slug}
              aria-label={`View project "${project.title}"`}
            >
              <Img
                alt={project.title}
                fluid={project.cover.childImageSharp.fluid}
              />
            </GridItem>
          ))}
        </Instagram>
      </Area>
    </Layout>
  );
};

export default Index;

export const query = graphql`
  query Index {
    firstProject: allProjectsYaml(skip: 6, limit: 1) {
      nodes {
        title
        slug
        cover {
          childImageSharp {
            fluid(quality: 95, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
    threeProjects: allProjectsYaml(limit: 3, skip: 3) {
      nodes {
        title
        slug
        cover {
          childImageSharp {
            fluid(quality: 95, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
    instagramFiles: allProjectsYaml(limit: 3) {
      nodes {
        title
        slug
        cover {
          childImageSharp {
            fluid(quality: 95, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
    aboutUs: file(
      sourceInstanceName: { eq: "images" }
      name: { eq: "about-b" }
    ) {
      childImageSharp {
        fluid(quality: 95, maxWidth: 1200) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
    instagram: file(
      sourceInstanceName: { eq: "images" }
      name: { eq: "instagram" }
    ) {
      childImageSharp {
        fluid(quality: 95, maxWidth: 1920) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`;
